/* Courses Section Specific Styles */
.courses-section {
    max-width: 1200px;
    margin: 2rem auto;
    padding: 0 1rem;
  }
  
  .course-form {
    background: white;
    padding: 1.5rem;
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    position: sticky;
    top: 80px; /* Below navbar */
    z-index: 100;
    margin-bottom: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
    align-items: end;
  }
  
  .courses-table {
    background: white;
    padding: 1.5rem;
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    margin-top: 1rem;
  }
  
  /* Sticky Table Headers */
  .courses-table thead {
    position: sticky;
    top: 160px; /* Below course form */
    background: var(--primary-color);
    z-index: 50;
  }
  
  /* Course Form Elements */
  .course-form input,
  .course-form select {
    padding: 0.8rem;
    border: 1px solid #ddd;
    border-radius: var(--border-radius);
    width: 100%;
    transition: all 0.3s ease;
  }
  
  .course-form input:focus,
  .course-form select:focus {
    border-color: var(--secondary-color);
    outline: none;
    box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
  }
  
  /* Course Table Enhancements */
  .course-row:hover {
    background-color: #f8f9fa;
  }
  
  .enrollment-progress {
    width: 100px;
    height: 8px;
    background: #eee;
    border-radius: 4px;
    overflow: hidden;
  }
  
  .enrollment-progress span {
    display: block;
    height: 100%;
    background: var(--success-color);
    transition: width 0.3s ease;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .course-form {
      position: static;
      grid-template-columns: 1fr;
      top: auto;
    }
    
    .courses-table thead {
      top: 0;
    }
  }
  
  /* Department Badges */
  .department-badge {
    display: inline-block;
    padding: 0.3rem 0.8rem;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 500;
    background: var(--light-color);
    color: var(--dark-color);
  }
  
  /* Hover Effects */
  .course-actions button {
    transition: transform 0.2s ease;
  }
  
  .course-actions button:hover {
    transform: translateY(-1px);
  }